<template>
	<section class="wrap login_wrap">
		<section class="wrap_in" :class="{ unscrollable: isUnscRollable }">
			<!-- 头部标题 -->
			<div class="header">
				<mt-header fixed :title="titles">
			      <mt-button icon="back" slot="left" @click.prevent="goBack"></mt-button>
				  <mt-button icon="more" slot="right" @click.prevent="showMore"></mt-button>
				</mt-header>
			</div>
			<!-- 头部标题 end -->

			<!-- 显示更多 -->
			<mt-popup
				class="more_block"
				v-model="isShowMore"
				position="top">
				<router-link to="/index">首页</router-link>
				<router-link to="/sort">分类</router-link>
				<router-link to="/myinfo">个人中心</router-link>
			</mt-popup>
			<!-- 显示更多 end -->

			<!-- 头部背景 -->
			<div class="login_bg">
				<div class="headimg">
					<img :src="hearImg.headImgCur" >
				</div>
				<p>{{ pageTitle }}</p>
			</div>
			<!-- 头部背景 end -->
			<!-- 信息 -->
			<div class="form_info">
				<form style="display:none">
				  <input type="password"/>
				</form>
				<input type="password" style="width:0;height:0;visibility:hidden"/>
				<!-- 登录 -->
				<div class="user_login" v-if="showId=='1'">
					<div class="form">
						<div>
							<input autocomplete="off" type="text" v-model="phone" placeholder="请输入手机号码">
						</div>
						<div>
							<input 
								autocomplete="off" 
								type="password" 
								v-model="pwd" 
								placeholder="请输入密码">
						</div>
						<button 
							:disabled="isNext"
							@click="signIn">登录</button>
					</div>
					<div class="links d_flex">
						<!-- <a @click="jumpPage('4', _self)" href="javascript:;">忘记密码?</a> -->
						<a @click="jumpPage('4', _self)" href="javascript:;">忘记密码?</a>
						<a @click="jumpPage('2', _self)" href="javascript:;">新用户注册</a>
					</div>
				</div>
				<!-- 登录 end -->
				<!-- 快速注册 -->
				<div class="user_register" v-if="showId=='2'">
					<div class="form">
						<div>
							<input autocomplete="off" type="text" v-model="registerPhone" placeholder="请输入手机号码">
						</div>
						<div>
							<input 
								autocomplete="off"
								type="text" 
								v-model="registerCode" 
								placeholder="请输入验证码"
								@keyup="isShowNext">
							<button :disabled="codeDisabled" @click="getCodeRegister"><i class="c_green">{{ registerText }}</i></button>
						</div>
						<button :disabled="isNext" @click="validate('3')">下一步</button>
					</div>
					<label ischeckboxtwo>
						<input autocomplete="off" @click="isShowNext" name="registerAgreement" type="checkbox" checked>
						<i></i>
						<span>我已阅读并同意<router-link to="/login_agreement">《网站服务协议》</router-link></span>
					</label>
				</div>
				<!-- 快速注册 end -->
				<!-- 快速注册 - 设置密码 -->
				<div class="user_login" v-if="showId=='3'">
					<div class="form">
						<div>
							<input autocomplete="off" type="password" v-model="registerPwd" placeholder="请输入密码">
						</div>
						<div>
							<input autocomplete="off" type="password" v-model="registerPwd2" placeholder="请输入密码确认">
						</div>
						<button 
							@click="clickRegister"
							:disabled='isNext'>确定</button>
					</div>
				</div>
				<!-- 快速注册 - 设置密码 end -->
				<!-- 忘记密码 step-1 -->
				<div class="forger_step_1" v-if="showId=='4'">
					<div class="form">
						<div>
							<input autocomplete="off" type="text" v-model="forgerTel_1" placeholder="请输入手机号码">
						</div>
						<div>
							<input autocomplete="off" type="text" v-model="forgerCode_1" placeholder="请输入验证码">
							<span>
								<img 
									class="ver_img" 
									:src="verificationSrc"
									@click="changeVerImg">
							</span>
						</div>
						<button 
							@click="forgetPwd"
							:disabled="isNext">下一步</button>
					</div>
				</div>
				<!-- 忘记密码 step-1 end -->
				<!-- 忘记密码 step-2 -->
				<div class="forger_step_2" v-if="showId=='5'">
					<div class="form">
						<div>
							<input 
								autocomplete="off"
								type="text" 
								v-model="forgerCode_2" 
								placeholder="请输入短信验证码">
							<!-- 获取了验证码之后显示 
							<span><i>56</i>S</span> -->
							<span 
								:disabled="codeDisabled"
								@click="getCodeTwo"><i class="c_green">{{ registerText }}</i></span>
						</div>
						<button 
							@click="verifyMsg"
							:disabled="isNext">下一步</button>
						<p>为保证您账户的安全,我们已发送了校验码到您的手机<span>{{ forgerTel_1 }}</span></p>
					</div>
				</div>
				<!-- 忘记密码 step-2 end -->
				<!-- 忘记密码 step-3 -->
				<div class="forger_step_3" v-if="showId=='6'">
					<div class="form">
						<div>
							<input autocomplete="off" type="password" v-model="newPwd" placeholder="请输入新密码">
						</div>
						<div>
							<input autocomplete="off" type="password" v-model="newPwd2" placeholder="请确认新密码">
						</div>
						<button
							:disabled="isNext"
							@click="comfireNewPwd">确定</button>
						<p>请输入6~20个英文字母、数字或符号（除空格）</p>
					</div>
				</div>
				<!-- 忘记密码 step-3 end -->
			</div>
			<!-- 信息 end -->
		</section>

		<!-- 子页面 -->
	    <transition name="fade">
	    	<router-view v-on:changeClass="changeClassOne"></router-view>
	    </transition>
	    <!-- 子页面 end -->
	</section>
</template>
<script>
import { Header, Button, Toast, MessageBox, Popup } from 'mint-ui'; 
import comHeaderTitle from '@/components/Common/ComHeaderTitle'
// import sha256 from 'sha-256-js';

export default {
	name: 'myinfo',
	data() {
		return {
		  hearImg: {
		  	headImgCur: './static/images/headimg.png', //默认为 登录头像
	  		headImgL: './static/images/headimg.png', //头像图片地址 登录
		  	headImgM: './static/images/headimg-2.png', //头像图片地址 找/设置密码
		  },
		  flag: false, //防止多次提交
		  isShowMore: false, // 是否显示更多
	      isUnscRollable: false, //是否页面不可滚动
	      backParentPage: false, //是否回到父页面
		  pageTitle: '用户登录', //本页面标题
		  // '1'登录，'2'快速注册，'3'，注册设置密码，'4'，忘记密码 步骤1，
		  showId: '1', //显示其他页的ID
		  isDetailPage: false,// 是否打开其他页
		  pwd: '', //登录密码
		  titles: '',
		  phone: '', //登录号码
		  nowPage: this.$route.query.np, //当前页，没有参数为登录，参数为2-注册页
		  //注册
		  registerPhone: '',// 注册手机号码
		  registerCode: '', //注册、忘记密码验证码
		  registerText: '获取验证码', //获取验证码文字
		  codeTime: 30, //重新获取验证码的时间
  		  timeText: 's后重新获取', //获取验证码倒计时文字
  		  codeDisabled: false, //是否禁止点击验证码按钮
  		  isNext: true, //是否禁止进行下一步
  		  codeNum: '', // 验证码验证成功返回
  		  registerPwd: '' ,//注册输入密码
  		  registerPwd2: '',//注册输入密码 确认密码
  		  //忘记密码
  		  verificationSrc: '', //忘记密码验证码
  		  forgerTel_1: '', //步骤1 电话
  		  forgerCode_1: '', //步骤1 图形验证码
  		  forgerCode_2: '', //步骤2 短信验证码
  		  forgerCode_3: '', //步骤3 6位随机验证密钥
  		  newPwd: '', //新密码
  		  newPwd2: '', //确认新密码
		}
	},
	props: [
		// 'pclass',
	],
	components: {
		'mt-header': Header,
		'mt-button': Button,
		comHeaderTitle, // 公共头部标题
		'mt-popup': Popup,
	},
	watch: {
		//登录 电话号码
		phone (){
			this.$options.methods.isClickBtn( this, 2, this.pwd, this.phone );
		},
		//登录 密码
		pwd (){
			this.$options.methods.isClickBtn( this, 2, this.pwd, this.phone );
		},
		//注册 设置密码
		registerPwd2 (){
			// if( this.registerPwd2 ){ 
			// 	//确认密码不为空时可以点击提交按钮
			// 	this.isNext = false ;
			// }else{
			// 	//确认密码不为空时不可以点击提交按钮
			// 	this.isNext = true ;
			// }
			this.$options.methods.isClickBtn( this, 1, this.registerPwd2 );
		},
		//忘记密码
		forgerTel_1 (){
			this.$options.methods.isClickBtn( this, 2, this.forgerTel_1, this.forgerCode_1 );
		},
		forgerCode_1 (){
			this.$options.methods.isClickBtn( this, 2, this.forgerTel_1, this.forgerCode_1 );
		},
		forgerCode_2 (){
			this.$options.methods.isClickBtn( this, 1, this.forgerCode_2 );
		},
		newPwd (){
			this.$options.methods.isClickBtn( this, 2, this.newPwd, this.newPwd2 );
		},
		newPwd2 (){
			this.$options.methods.isClickBtn( this, 2, this.newPwd, this.newPwd2 );
		},
	},
	created : function(){
		// console.log(this.nowPage)
		if(this.nowPage==2){
			this.pageTitle = '快速注册';
			this.showId = 2;
		}
	},
	mounted : function() {
		const _self = this;
		this.$emit('changeClass',false);
	},
	updated (){
	    this.$nextTick(function () {
	      if( !this.backParentPage ){
	        this.isUnscRollable = true;
	      }
	    }) 
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
  	methods: {
  		//登录
  		signIn (){
  			const _self = this;
  			var checkPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
  			if( !checkPhone.test(this.phone) ){
				this.$options.methods.comToast( '请输入正确的手机号码' );

				this.phone = this.pwd = '';
				return false;
  			}

  			if( this.flag ){return}
  			this.flag = true;
  			this.$ajax({
  				// url: '/server/login',
  				url: _self.webRoot + '/login',
  				data: {
  					username: _self.phone,
  					password: _self.pwd
  				}
  			}).then(function(res){
  				// console.log(res)
  				var memberId = '';

  				if( !res.data.success ){
  					// _self.$options.methods.comToast( res.data.msg );
  					MessageBox.alert(res.data.msg).then(action => {
					  // console.log('action: '+action)
					});
					_self.flag = false;
  					return false;
  				}

  				memberId = res.data.memberID;
  				// sessionStorage.seslomemberId = memberId; //会员ID 
				// 储存个人信息
  				_self.$store.commit('add', {
  					addMemberInfo: {memberID: res.data.memberID},
  				});

				//会员查询 
				_self.$ajax({
					url: _self.webRoot + '/vipSearchByID',
					data: {
						id: memberId,
					}
				})
				.then(function(res){
					// console.log('获取个人信息', res)
					if( !res.data ){
						// console.log('获取个人信息失败')
						_self.$options.methods.comToast( '获取个人信息失败' );
						_self.flag = false;
						return false;
					}
					// 储存个人信息
	  				_self.$store.commit('add', {
	  					addMemberInfo: res.data,
	  				});
	  				//会员ID进行加密
	  				// res.data.memberID = sha256(res.data.memberID + 'akjk');
	  				// console.log(res.data)
	  				setTimeout(function(){
						_self.$router.replace({ path: '/index' });
						_self.flag = false;
	  				},500)
				})
				.catch(function(err){
					console.log(err)
				});
  			}).catch(function(error){
  				console.log(error)
  			});
  		},
  		//忘记密码 - 找回密码 - 安全监测
  		forgetPwd: function( event, _self ){
  			var _self = _self ? _self : this;
  			var checkPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
  			var timer = null;

  			if( !checkPhone.test(_self.forgerTel_1) ){
  		// 		Toast({
				//   message: '请输入正确的手机号码',
				//   position: 'center',
				//   duration: 2000
				// });
				_self.$options.methods.comToast( '请输入正确的手机号码' );

				_self.forgerTel_1 = _self.forgerCode_1 = '';
				return false;
  			}

  			_self.$ajax({
  				url: _self.webRoot + '/verifyCode',
  				data: {
  					telephone: _self.forgerTel_1,
  					code: _self.forgerCode_1,
  				}
  			})
  			.then(function(res){
  				// console.log(res)
  				if( res.data.msg === true ){
  					_self.$options.methods.jumpPage('5', _self);

		  			_self.codeDisabled = true; // 禁止点击获取验证按钮
		  			_self.registerText = _self.codeTime + _self.timeText;
		  			clearInterval(timer);
		  			timer = setInterval(function(){
		  				_self.codeTime--;
		  				_self.registerText = _self.codeTime + _self.timeText;

		  				if(_self.codeTime == 0){
							clearInterval(timer);
		  					_self.registerText = '获取验证码';
		  					_self.codeDisabled = false; // 开放点击获取验证按钮
		  					_self.codeTime = 30;
		  				}
		  			},1000);

  					_self.isNext = true;
  				}else{
  					_self.$options.methods.comToast( res.data.msg );
  					_self.isNext = true;
  					return false;
  				}
  			})
  			.catch(function(err){
  				console.log(err)
  			});
  		},
  		//更换验证码
  		changeVerImg (){
  			const _self = this;
  			var verificationSrc = this.verificationSrc.split('?');
  			var randomNum = Math.random() * 100;

  			this.verificationSrc = verificationSrc[0] + '?v=' + randomNum;
  		},
  		// 安全监测 - 再获取短信验证码
  		getCodeTwo ( event ){
  			const _self = this;
  			// this.$options.methods.forgetPwd( event, this );
  			var verificationSrc = this.verificationSrc.split('?');
  			var randomNum = Math.random() * 100;

  			this.forgerCode_1 = this.forgerCode_2 = '';
  			this.verificationSrc = verificationSrc[0] + '?v=' + randomNum;
			this.$options.methods.jumpPage(4, _self);
  		},
  		//忘记密码 - 安全监测-校验验证码
  		verifyMsg (){
  			const _self = this;
  			var timer = null;
  			//测试
			// _self.$options.methods.jumpPage('6', _self);
			// return false;

			this.$ajax({
				url: _self.webRoot + '/verifyMessage',
				data: { 
					telephone: _self.forgerTel_1 ,
					randomCode: _self.forgerCode_2,
				}
			}).then(function(res){
				// console.log(res)
				if ( res.data.num ){
					_self.forgerCode_3 = res.data.num ;
					_self.$options.methods.jumpPage(6, _self);
				}else{
					_self.$options.methods.comToast( res.data.msg );
				}
			}).catch(function(err){
				_self.$options.methods.comToast( '服务出错，' + err );
			});
  		},
  		//确认新密码
  		comfireNewPwd (){
  			const _self = this;
  			const checkPwd = /^[0-9A-Za-z!#$%^&*]{6,20}$/;

  			if( this.flag ){ 
  				return false; 
  			};
  			this.flag = true ;

  			if ( this.newPwd == this.newPwd2 ){
  				if( !checkPwd.test( this.newPwd ) ){
  					_self.$options.methods.comToast( '请输入6~20个英文字母、数字或符号（除空格）', 1500 );
  					_self.flag = false ;
  					return false;
  				}
  				_self.$ajax({
  					url: _self.webRoot + '/changePassword',
  					data: {
  						telephone: _self.forgerTel_1,
  						randomCodeTwo: _self.forgerCode_3,
  						password: _self.newPwd
  					}
  				})
  				.then(function(res){
  					// console.log(res)
  					if( res.data ){
  						_self.$options.methods.comToast( '密码修改成功！', 1500 );
  						setTimeout(function(){
  							// console.log('xgmmcgl')
  							// _self.$router.replace({ path: 'login' });
  							location.reload();
  						},1000);
  					}else{
  						_self.$options.methods.comToast( res.data.msg );
  					}
  					_self.flag = false;
  				})
  				.catch(function(err){
  					console.log(err)
  				})
  			}else{
  				_self.$options.methods.comToast( '两次密码输入不正确！' );
				_self.flag = false;
  			}
  		},
  		//注册 - 获取短信验证码
  		getCodeRegister (){
  			const _self = this;
  			var checkPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
  			var timer = null;

  			if( this.flag ){ 
  				return false; 
  			};
  			this.flag = true ;
  			if( !this.registerPhone || !checkPhone.test(this.registerPhone) ){
				this.$options.methods.comToast( '请输入正确的手机号码' );
				return false;
  			}

			this.$ajax({
				url: _self.webRoot + '/sendMessage',
				data: { telephone: _self.registerPhone }
			}).then(function(res){
				// console.log(res)
				if( res.data.msg === true ){
					// console.log('可以下一步');
		  			_self.codeDisabled = true; // 禁止点击获取验证按钮
		  			_self.registerText = _self.codeTime + _self.timeText;
		  			clearInterval(timer);
		  			timer = setInterval(function(){
		  				_self.codeTime--;
		  				_self.registerText = _self.codeTime + _self.timeText;

		  				if(_self.codeTime == 0){
							clearInterval(timer);
		  					_self.registerText = '获取验证码';
		  					_self.codeDisabled = false; // 开放点击获取验证按钮
		  					_self.codeTime = 30;
		  				}
		  			},1000);
				}else{
					_self.$options.methods.comToast( res.data.msg );
				}
				_self.flag = false;
			}).catch(function(err){
				_self.$options.methods.comToast( '服务出错，' + err );
				_self.flag = false;
			});
  		},
  		// 注册 - 下一步
  		validate ( jumpId ){
  			const _self = this;

			this.registerPwd = this.registerPwd2 = ''; // 清空数据
			this.$ajax({
				// url: '/server/verifyMessage',
				url: _self.webRoot + '/verifyMessage',
				data: { telephone: _self.registerPhone, randomCode: _self.registerCode }
			}).then(function(res){
				// console.log(res)
				if( res.data.num ){
					// console.log('可以下一步');
					_self.codeNum = res.data.num ;
  					_self.$options.methods.jumpPage(jumpId, _self);
  					_self.isNext = true;
				}else{
					_self.$options.methods.comToast( res.data.msg );
					return false;
				}
			})
  		},
  		//点击验证码输入框
  		isShowNext (){
  			var registerAgreement = document.getElementsByName('registerAgreement')[0].checked;

  			if( this.registerCode && registerAgreement ){ //有验证码并且同意协议
  				this.isNext = false;
  				return false;
  			}
  			if( !this.registerCode || !registerAgreement ){
				this.isNext = true;
  				return false;
  			}
  		},
  		//确定注册
  		clickRegister (){
  			const _self = this;
  			const checkPwd = /^[0-9A-Za-z!#$%^&*]{6,20}$/;

  			if( this.flag ){ 
  				return false; 
  			};
  			this.flag = true ;

  			if( this.registerPwd != this.registerPwd2 ){
				this.$options.methods.comToast( '两次密码输入不正确' );
				this.registerPwd = this.registerPwd2 = '';
				_self.flag = false ;
				return false;
  			}
			if( !checkPwd.test( this.registerPwd ) ){
				_self.$options.methods.comToast( '请输入6~20个英文字母、数字或符号（除空格）', 1500 );
				_self.flag = false ;
				return false;
			}
  			this.$ajax({
  				// url: '/server/vipRegisterInsert',
  				url: _self.webRoot + '/vipRegisterInsert',
  				data: {telephone: _self.registerPhone, randomCodeTwo: _self.codeNum, password: _self.registerPwd}
  			}).then(function(res){
  				// console.log(res)
  				if( res.data ){
  					// _self.$router.replace({ path: '/success_registre' });
  					_self.$router.push({ path: '/success_registre' });
  				}else{
					_self.$options.methods.comToast( res.data.msg );
  				}
  				_self.flag = false;
  			});
  		},

  		//公共函数
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
	    //是否可以点击下一步的按钮
	    isClickBtn ( _self, num, data1, data2 ){
	    	if( num >= 2 ){
	    		//有两个以上参数
				if( data1 && data2 ){
					//输入密码 可点击登录按钮
					_self.isNext = false ;
				}else{
					_self.isNext = true ;
				}
	    	}else{
				if( data1 ){
					//输入密码 可点击登录按钮
					_self.isNext = false ;
				}else{
					_self.isNext = true ;
				}
	    	}
	    },
	    //跳转链接
	    jumpPage ( index, _self ){
	    	var pageTitle = 
	    		index == '2' ? '快速注册' : 
	    		index == '3' ? '设置密码' : 
	    		index == '4' ? '找回密码' : 
	    		index == '5' ? '安全检测' :  
	    		index == '6' ? '设置新密码' : '用户登录';

    		if( index == '4' ){ //更新忘记密码验证码
    			// _self.verificationSrc = '/AKGW-api/v1/sendCode?v=' + Math.random();
    			_self.verificationSrc = _self.webRoot + '/sendCode?v=' + Math.random();
    		}
    		
    		if( index == '4' || index == '5' || index == '6' ){
    			//设置/找回密码头像
    			_self.hearImg.headImgCur = _self.hearImg.headImgM;
    		}else{
    			//登录头像
    			_self.hearImg.headImgCur = _self.hearImg.headImgL;
    		}

	    	_self.isDetailPage = true; // 此时进入模块中
    		_self.pageTitle = pageTitle; // 模块标题
	    	_self.showId = index; //显示的模块
	    },
	    //提示框
	    comToast ( msg, time ){
	    	var time = time ? time : 2000;

			Toast({
			  message: msg,
			  position: 'center',
			  duration: time
			});
	    },
		// 后退
		goBack (){	
			if( this.isDetailPage ){ //此时进入模块中,点击后退到登录页面
				this.showId =  this.nowPage==2 ?'2':'1';
	    		this.isDetailPage = false;
	    		this.pageTitle = this.nowPage==2 ?'快速注册':'用户登录';
	    		//登录头像回到登录头像
    			this.hearImg.headImgCur = this.hearImg.headImgL;
    			this.$options.methods.clearData(this);

	    		return false;
			}
			this.$router.go(-1);
		},
		//显示更多
		showMore (){
			this.isShowMore = true;
		},
		//清除数据
		clearData ( _self ){
		  _self.flag = false; //防止多次提交
	      _self.isUnscRollable = false; //是否页面不可滚动
	      _self.backParentPage = false; //是否回到父页面
		  _self.pageTitle = '用户登录'; //本页面标题
		  // '1'登录，'2'快速注册，'3'，注册设置密码，'4'，忘记密码 步骤1，
		  _self.showId = '1'; //显示其他页的ID
		  _self.isDetailPage = false;// 是否打开其他页
		  _self.pwd = ''; //登录密码
		  _self.titles = '';
		  _self.phone = ''; //登录号码
		  _self.nowPage = _self.$route.query.np; //当前页，没有参数为登录，参数为2-注册页
		  //注册
		  _self.registerPhone = '';// 注册手机号码
		  _self.registerCode = ''; //注册、忘记密码验证码
		  _self.registerText = '获取验证码'; //获取验证码文字
		  _self.codeTime = 30; //重新获取验证码的时间
  		  _self.timeText = 's后重新获取'; //获取验证码倒计时文字
  		  _self.codeDisabled = false; //是否禁止点击验证码按钮
  		  _self.isNext = true; //是否禁止进行下一步
  		  _self.codeNum = ''; // 验证码验证成功返回
  		  _self.registerPwd = '' ;//注册输入密码
  		  _self.registerPwd2 = '';//注册输入密码 确认密码
  		  //忘记密码
  		  _self.verificationSrc = ''; //忘记密码验证码
  		  _self.forgerTel_1 = ''; //步骤1 电话
  		  _self.forgerCode_1 = ''; //步骤1 图形验证码
  		  _self.forgerCode_2 = ''; //步骤2 短信验证码
  		  _self.forgerCode_3 = ''; //步骤3 6位随机验证密钥
  		  _self.newPwd = ''; //新密码
  		  _self.newPwd2 = ''; //确认新密码
		},
  	}
}
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
</style>

<style scoped>
@import './Css/Login.css';
</style>



